import React from "react";
import Stack from "@mui/material/Stack";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Autocomplete from "@mui/material/Autocomplete";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import FormLabel from "@mui/material/FormLabel";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import Typography from "@mui/material/Typography";
import moment from "moment";
import {
  ActionDialog,
  Barcode,
  CustomDialog,
  CustomInput,
  TopNav,
} from "../../components";
import { useNavigate } from "react-router-dom";
import makeStyles from "@mui/styles/makeStyles";
import { connect } from "react-redux";
import {
  getAgama,
  getJenisIdentitas,
  getStatusNikah,
  getDataProduk,
  setFormVaksin,
  resetVaksin,
  setIdentitas,
  getIdentitas,
  registerVaksin,
  resetIdentitas,
  getPropinsi,
  getKotamadya,
  getKecamatan,
  getKelurahan,
} from "../../redux/actions";
import { isFormValid, isSelectedFieldValid } from "./validation";



const useStyles = makeStyles(() => ({
  root: {
    padding: "16px 0",
  },
  langContainer: {
    padding: "16px 0",
    overflowX: "hidden",
  },
  dialogImg: {
    height: "250px",
    width: "auto",
  },
}));
const RegisterVaksin = (props) => {
  const {
    stateBahasa,
    actGetAgama,
    actGetJenisID,
    actGetStatusNikah,
    stateAgama,
    stateStatusNikah,
    stateJenisID,
    stateError,
    actRegisterVaksin,
    actGetProduk,
    stateProduk,
    statePropinsi,
    stateKotamadya,
    stateKecamatan,
    stateKelurahan,
    actSetFormVaksin,
    stateRegVaksin,
    actResetVaksin,
    actSetIdentitas,
    stateIdentitas,
    actGetIdentitas,
    actResetIdentitas,
    actGetPropinsi,
    actGetKotamadya,
    actGetKecamatan,
    actGetKelurahan,
  } = props;
  const {
    jenisId,
    jenisIdnomor,
    namaLengkap,
    tempatLahir,
    tglLahir,
    jenisKelamin,
    alamat,
    rt,
    rw,
    kelurahan,
    kecamatan,
    kotaDati,
    kodePos,
    propinsi,
    noTelpon,
    agama,
    statusNikah,
    alamatDomisili,
  } = stateIdentitas;

  const { produkId, tglKunjungan } = stateRegVaksin;

  const invalidInitial = {
    jenisId: "",
    produkId: "",
    jenisIdnomor: "",
    namaLengkap: "",
    tempatLahir: "",
    tglLahir: "",
    jenisKelamin: "",
    alamat: "",
    rt: "",
    rw: "",
    kelurahan: "",
    kecamatan: "",
    kotaDati: "",
    kodePos: "",
    propinsi: "",
    noTelpon: "",
    agama: "",
    statusNikah: "",
    alamatDomisili: "",
    tglKunjungan: "",
  };

  const classes = useStyles();
  const [openFail, setOpenFail] = React.useState(false);
  const [sameAddress, setHandleSameAddress] = React.useState(false);
  const [errorForm, setErrorForm] = React.useState("");
  const [succForm, setSuccForm] = React.useState(false);

  //   const [form, setForm] = React.useState(formInitial);
  const [formInvalid, setInvalid] = React.useState(invalidInitial);
  const [openBahasa, setOpenBahasa] = React.useState(true);

  const navigate = useNavigate();
  React.useEffect(() => {
    if (stateError) {
      navigate("/exception-handling");
    }
  }, [stateError]);

  React.useEffect(() => {
    actGetAgama();
    actGetJenisID();
    actGetStatusNikah();
    actGetProduk();
     actGetPropinsi();
  }, []);

  const changeForm = (elem, type) => {
    let { name, id, value } = elem.target;
    let { error } = isSelectedFieldValid(name ? name : id, value);
    setInvalid({
      ...formInvalid,
      [name ? name : id]: error ? "*" + error.message : "",
    });
    if (type === "identitas") {
      actSetIdentitas({
        key: name ? name : id,
        value: value,
      });
    } else {
      actSetFormVaksin({
        key: name ? name : id,
        value: value,
      });
    }
    // return setForm({
    //   ...form,
    //   [name ? name : id]: value,
    // });
    // return;
  };

  const changeFormAuto = (elem) => {
    let { name, id, value } = elem;
    let { error } = isSelectedFieldValid(name, value);
    switch (name) {
      case "propinsi":
        actGetKotamadya(id);
        break;
      case "kotaDati":
        actGetKecamatan(id);
        break;
      case "kecamatan":
        actGetKelurahan(id);
        break;
      case "kelurahan":
        break;
      default:
        break;
    }
    setInvalid({
      ...formInvalid,
      [name]: error ? "*" + error.message : "",
    });
    actSetIdentitas({
      key: name,
      value: value,
    });
  };
  const handleGetNIK = () => {
    if (jenisId === "1" && jenisIdnomor.length === 16) {
      actGetIdentitas(jenisIdnomor);
    }
  };
  const [submitted, setSubmitted] = React.useState(false);
  const handleSubmit = () => {
    let body = {
      ...stateRegVaksin,
      jenisId,
      jenisIdnomor,
      namaLengkap,
      tempatLahir,
      tglLahir,
      jenisKelamin,
      alamatDomisili,
      alamat,
      rt,
      rw,
      kelurahan,
      kecamatan,
      kotaDati,
      kodePos,
      noTelpon,
      agama,
      statusNikah,
      propinsi,
    };
    let isValid = isFormValid(body);
    setSubmitted(true);
    if (!isValid.error) {
      return actRegisterVaksin(body, {
        success: () => {
          actResetVaksin();
          setSuccForm(true);
          setSubmitted(false);
        },
        failed: (msg) => {
          setErrorForm(msg);
          setSubmitted(false);
        },
      });
    } else {
      if (
        isValid.error.details[0].context &&
        isValid.error.details[0].context.key
      ) {
        let key = isValid.error.details[0].context.key;
        setInvalid({
          ...formInvalid,
          [key]: isValid.error.details[0].message,
        });
      }
      setErrorForm(isValid.error.details[0].message || "Mohon Lengkapi Form!.");
      setSubmitted(false);
    }
  };

  const handleSameAddress = () => {
    setHandleSameAddress(!sameAddress);
    // setForm({ ...form, alamat: alamatDomisili });
    actSetIdentitas({
      key: "alamatDomisili",
      value: alamat,
    });
  };
  return (
    <>
      {/* <CustomDialog
        setOpen={setSuccForm}
        type={"success"}
        title={"Berhasil Terdaftar!."}
        open={succForm}
      /> */}
      <ActionDialog open={succForm} buttonText={"Close"} noClose={true}>
        <Typography variant="subtitle2" align="center">
          Berhasil Mendaftar!.
        </Typography>
        <Barcode
          barcode={jenisIdnomor}
          action={() => {
            actResetIdentitas();
            setSuccForm(false);
          }}
        />
      </ActionDialog>
      <CustomDialog
        setOpen={setErrorForm}
        type={"error"}
        title={errorForm}
        open={errorForm ? true : false}
      />
      <Container className={classes.root}>
        <Stack spacing={2}>
          <TopNav
            type="back"
            title={
              stateBahasa === "indonesia" ? "Daftar Vaksin" : "Register Vaccination"
            }
            click={() => navigate(-1)}
          />
          <FormControl
            fullWidth
            required
            error={formInvalid.produkId ? true : false}
          >
            <InputLabel id="produkId">
              {stateBahasa === "indonesia" ? "Jenis Vaksin" : "Vaccine Type"}
            </InputLabel>
            <Select
              labelId="produkId"
              id="produkId"
              name="produkId"
              value={produkId}
              onChange={(el) => changeForm(el, "vaksin")}
            >
              {stateProduk &&
                stateProduk.map((item, index) => {
                  return (
                    <MenuItem
                      key={"vaksin" + index}
                      value={item.ref_prod_id.toString()}
                    >
                      {item.ref_prod_nama}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>

          <FormControl
            fullWidth
            required
            error={formInvalid.jenisId ? true : false}
          >
            <InputLabel id="jenisId">
              {stateBahasa === "indonesia" ? "Jenis Identitas" : "Identity Type"}
            </InputLabel>
            <Select
              labelId="jenisId"
              id="jenisId"
              name="jenisId"
              value={jenisId}
              onChange={(el) => changeForm(el, "identitas")}
            >
              {stateJenisID &&
                stateJenisID.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.ref_jki_id}
                      value={item.ref_jki_id.toString()}
                    >
                      {item.ref_jki_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            id="jenisIdnomor"
            name="jenisIdnomor"
            label={stateBahasa === "indonesia" ? "No Identitas" : "Identity Number"}
            required
            error={formInvalid.jenisIdnomor ? true : false}
            value={jenisIdnomor}
            onChange={(el) => changeForm(el, "identitas")}
            onBlur={handleGetNIK}
            helperText={formInvalid.jenisIdnomor}
          />
          <TextField
            id="namaLengkap"
            name="namaLengkap"
            label={stateBahasa === "indonesia" ? "Nama Lengkap" : "Fullname"}
            required
            value={namaLengkap}
            error={formInvalid.namaLengkap ? true : false}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.namaLengkap}
          />
          <TextField
            id="tempatLahir"
            name="tempatLahir"
            label={stateBahasa === "indonesia" ? "Tempat Lahir" : "Birthplace"}
            // label="Tempat Lahir - Birthplace"
            required
            value={tempatLahir}
            onChange={(el) => changeForm(el, "identitas")}
            error={formInvalid.tempatLahir ? true : false}
            helperText={formInvalid.tempatLahir}
          />
          <TextField
            id="tglLahir"
            name="tglLahir"
            label={stateBahasa === "indonesia" ? "Tanggal Lahir" : "Birthdate"}
            // label="Tanggal Lahir - Birthdate"
            required
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{
              max: moment().format("YYYY-MM-DD"),
            }}
            type={"date"}
            value={tglLahir}
            onChange={(el) => changeForm(el, "identitas")}
            error={formInvalid.tglLahir ? true : false}
            helperText={formInvalid.tglLahir}
          />
          <FormControl required error={formInvalid.jenisKelamin ? true : false}>
            <FormLabel id="jenisKelamin">
              {stateBahasa === "indonesia" ? "Jenis Kelamin" : "Gender"}
            </FormLabel>
            {/* <FormLabel id="jenisIdentitas">Jenis Kelamin - Gender</FormLabel> */}
            <RadioGroup
              row
              aria-labelledby="jeniKelamin"
              id="jenisKelamin"
              name="jenisKelamin"
              value={jenisKelamin}
              onChange={(el) => changeForm(el, "identitas")}
            >
              <FormControlLabel
                value="1"
                control={<Radio />}
                label={stateBahasa === "indonesia" ? "Pria" : "Male"}
                // label="Pria - Male"
              />
              <FormControlLabel
                value="2"
                control={<Radio />}
                label={stateBahasa === "indonesia" ? "Perempuan" : "Female"}

                // label="Perempuan - Female"
              />
            </RadioGroup>
          </FormControl>
          <Autocomplete
            id="propinsi"
            name="propinsi"
            freeSolo
            // onChange={(el) => console.log(el.target)}
            value={propinsi}
            onChange={(el, val) =>
              changeFormAuto({
                name: "propinsi",
                value: val.label,
                id: val.id,
              })
            }
            inputValue={propinsi}
            onInputChange={(event, newInputValue) =>
              changeForm(
                {
                  target: {
                    name: "propinsi",
                    value: newInputValue,
                  },
                },
                "identitas"
              )
            }
            options={statePropinsi.map((option) => {
              return {
                id: option.ref_propinsi_id,
                label: option.ref_propinsi_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={stateBahasa === "indonesia" ? "Provinsi" : "Province"}
                required
                helperText={formInvalid.propinsi}
                error={formInvalid.propinsi ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kotaDati"
            name="kotaDati"
            freeSolo
            inputValue={kotaDati}
            onInputChange={(event, newInputValue) => {
              changeForm(
                {
                  target: {
                    name: "kotaDati",
                    value: newInputValue,
                  },
                },
                "identitas"
              );
            }}
            value={kotaDati}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kotaDati",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKotamadya.map((option) => {
              return {
                id: option.ref_kotadati2_id,
                label: option.ref_kotadati2_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={
                  stateBahasa === "indonesia" ? "Kotamadya / Dati II" : "Regency"
                }
                required
                helperText={formInvalid.kotaDati}
                error={formInvalid.kotaDati ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kecamatan"
            name="kecamatan"
            freeSolo
            inputValue={kecamatan}
            onInputChange={(event, newInputValue) => {
              changeForm(
                {
                  target: {
                    name: "kecamatan",
                    value: newInputValue,
                  },
                },
                "identitas"
              );
            }}
            value={kecamatan}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kecamatan",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKecamatan.map((option) => {
              return {
                id: option.ref_kcamatan_id,
                label: option.ref_kcamatan_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={stateBahasa === "indonesia" ? "Kecamatan" : "Country"}
                required
                helperText={formInvalid.kecamatan}
                error={formInvalid.kecamatan ? true : false}
              />
            )}
          />
          <Autocomplete
            id="kelurahan"
            name="kelurahan"
            freeSolo
            inputValue={kelurahan}
            onInputChange={(event, newInputValue) => {
              changeForm(
                {
                  target: {
                    name: "kelurahan",
                    value: newInputValue,
                  },
                },
                "identitas"
              );
            }}
            value={kelurahan}
            onChange={(el, val) =>
              changeFormAuto({
                name: "kelurahan",
                value: val.label,
                id: val.id,
              })
            }
            options={stateKelurahan.map((option) => {
              return {
                id: option.ref_klurahan_id,
                label: option.ref_klurahan_nama,
              };
            })}
            renderInput={(params) => (
              <TextField
                {...params}
                fullWidth
                label={stateBahasa === "indonesia" ? "Kelurahan" : "Subdistrict"}
                required
                helperText={formInvalid.kelurahan}
                error={formInvalid.kelurahan ? true : false}
              />
            )}
          />
          {/* <TextField
            fullWidth
            id="propinsi"
            name="propinsi"
            // label="Provinsi - Province"
            label={stateBahasa === "indonesia" ? "Provinsi" : "Province"}
            required
            value={propinsi}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.propinsi}
            error={formInvalid.propinsi ? true : false}
          />
          <TextField
            fullWidth
            id="kecamatan"
            // label="Kecamatan - Country"
            label={stateBahasa === "indonesia" ? "Kecamatan" : "Country"}
            required
            value={kecamatan}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kecamatan}
            error={formInvalid.kecamatan ? true : false}
          />
          <TextField
            fullWidth
            id="kotaDati"
            name="kotaDati"
            // label="Kotamadya / Dati II - Regency"
            label={stateBahasa === "indonesia" ? "Kotamadya / Dati II" : "Regency"}
            required
            value={kotaDati}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kotaDati}
            error={formInvalid.kotaDati ? true : false}
          />
          <TextField
            fullWidth
            id="kelurahan"
            name="kelurahan"
            // label="Kelurahan - Subdistrict"
            label={stateBahasa === "indonesia" ? "Kelurahan" : "Subdistrict"}
            required
            value={kelurahan}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kelurahan}
            error={formInvalid.kelurahan ? true : false}
          /> */}
          <TextField
            fullWidth
            id="kodePos"
            name="kodePos"
            // label="Kode Pos - Post Code"
            label={stateBahasa === "indonesia" ? "Kode Pos" : "Post Code"}
            required
            value={kodePos}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.kodePos}
            error={formInvalid.kodePos ? true : false}
          />
          <TextField
            fullWidth
            id="alamat"
            name="alamat"
            // label="Alamat Sesuai Identitas - Address ID"
            label={
              stateBahasa === "indonesia" ? "Alamat Sesuai Identitas" : "Address ID"
            }
            required
            value={alamat}
            onChange={(el) => changeForm(el, "identitas")}
            helperText={formInvalid.alamat}
            error={formInvalid.alamat ? true : false}
          />
          <div>
            <Stack direction="row" spacing={2}>
              <TextField
                fullWidth
                id="rt"
                name="rt"
                label="RT"
                required
                value={rt}
                onChange={(el) => changeForm(el, "identitas")}
                helperText={formInvalid.rw}
                error={formInvalid.rt ? true : false}

                // value={type}
                // onChange={changeType}
              />
              <TextField
                fullWidth
                id="rw"
                name="rw"
                label="RW"
                required
                value={rw}
                onChange={(el) => changeForm(el, "identitas")}
                helperText={formInvalid.rw}
                error={formInvalid.rw ? true : false}
              />
            </Stack>
          </div>
          <Stack>
            <TextField
              id="alamatDomisili"
              name="alamatDomisili"
              // label="Alamat Domisili - Current Address"
              label={
                stateBahasa === "indonesia" ? "Alamat Domisili" : "Current Address"
              }
              required
              error={formInvalid.alamatDomisili ? true : false}
              value={alamatDomisili}
              disabled={sameAddress}
              onChange={(el) => changeForm(el, "identitas")}
              helperText={formInvalid.alamatDomisili}
            />
            <FormGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={sameAddress}
                    onChange={handleSameAddress}
                  />
                }
                label={
                  stateBahasa === "indonesia"
                    ? "Sama dengan alamat KTP"
                    : "Same as ID address"
                }
              />
            </FormGroup>
          </Stack>
          <Stack direction="row" spacing={2}>
            <CustomInput
              id="noTelpon"
              name="noTelpon"
              label={stateBahasa === "indonesia" ? "No Telp 1" : "Phone Number 1"}
              fullWidth
              required
              value={noTelpon}
              onChange={(el) => changeForm(el, "identitas")}
              helperText={formInvalid.noTelpon}
              className={classes.inputPhone}
              inputType="phone-number"
              error={formInvalid.noTelpon ? true : false}
            />
          </Stack>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">Agama - Religion</InputLabel> */}
            <InputLabel id="agama">
              {stateBahasa === "indonesia" ? "Agama" : "Religion"}
            </InputLabel>
            <Select
              labelId="agama"
              id="agama"
              name="agama"
              value={agama}
              onChange={(el) => changeForm(el, "identitas")}

              //   value={age}
              //   label="Age"
              //   onChange={handleChange}
            >
              {stateAgama &&
                stateAgama.map((item) => {
                  return (
                    <MenuItem
                      key={"agama" + item.ref_agama_id}
                      value={item.ref_agama_id}
                    >
                      {item.ref_agama_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <FormControl fullWidth required>
            {/* <InputLabel id="agama">
              Status Pernikahan - Marital Status
            </InputLabel> */}
            <InputLabel id="agama">
              {stateBahasa === "indonesia" ? "Status Pernikahan" : "Marital Status"}
            </InputLabel>
            <Select
              labelId="statusNikah"
              id="statusNikah"
              name="statusNikah"
              value={statusNikah}
              onChange={(el) => changeForm(el, "identitas")}
            >
              {stateStatusNikah &&
                stateStatusNikah.map((item) => {
                  return (
                    <MenuItem
                      key={"jenisID" + item.stat_id}
                      value={item.stat_id}
                    >
                      {item.stat_ket}
                    </MenuItem>
                  );
                })}
            </Select>
          </FormControl>
          <TextField
            id="tglKunjungan"
            name="tglKunjungan"
            label={stateBahasa === "indonesia" ? "Tanggal Kunjungan" : "Visit Date"}
            // label="Tanggal Lahir - Birthdate"
            required
            InputLabelProps={{
              shrink: true,
            }}
            inputProps={{
              min: moment().add(1, "days").format("YYYY-MM-DD"),
              max: moment().add(3, "days").format("YYYY-MM-DD"),
            }}
            // min={moment().format("YYYY-MM-DD")}
            // max={moment().add(3, "days").format("YYYY-MM-DD")}
            type={"date"}
            value={tglKunjungan}
            onChange={(el) => changeForm(el, "vaksin")}
            error={formInvalid.tglKunjungan ? true : false}
            helperText={formInvalid.tglKunjungan}
          />
          <Button
            variant="contained"
            onClick={handleSubmit}
            disabled={submitted}
            // onClick={() => navigate("/")}
          >
            {stateBahasa === "indonesia" ? "Daftar" : "Submit"}
          </Button>
        </Stack>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateProduk: state.master.produk,
    stateIdentitas: state.auth.identitas,
    stateAgama: state.master.agama,
    stateJenisID: state.master.jenisIdentitas,
    stateStatusNikah: state.master.statusNikah,
    stateRegVaksin: state.register.vaksin,
    stateError: state.app.error,
    statePropinsi: state.master.propinsi,
    stateKotamadya: state.master.kotamadya,
    stateKecamatan: state.master.kecamatan,
    stateKelurahan: state.master.kelurahan,
    stateBahasa: state.app.bahasa
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actResetVaksin: () => dispatch(resetVaksin()),
    actResetIdentitas: () => dispatch(resetIdentitas()),
    actGetIdentitas: (nik) => dispatch(getIdentitas(nik)),
    actSetFormVaksin: (body) => dispatch(setFormVaksin(body)),
    actSetIdentitas: (body) => dispatch(setIdentitas(body)),
    actGetProduk: (action) => dispatch(getDataProduk(action)),
    actGetAgama: (action) => dispatch(getAgama(action)),
    actGetJenisID: (action) => dispatch(getJenisIdentitas(action)),
    actGetStatusNikah: (action) => dispatch(getStatusNikah(action)),
    actRegisterVaksin: (body, action) => dispatch(registerVaksin(body, action)),
    actGetPropinsi: (body) => dispatch(getPropinsi(body)),
    actGetKotamadya: (body) => dispatch(getKotamadya(body)),
    actGetKecamatan: (body) => dispatch(getKecamatan(body)),
    actGetKelurahan: (body) => dispatch(getKelurahan(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RegisterVaksin);
